                </div>
            </section>
            <ul id="citylist" style="display:none"></ul>
    </div>
</template>
<script>
    

    import './picker/cityData.js'
    
    import './picker/city.js'
    

    export default{
        
        data(){
            
            return{
            
                province : "",
                
                city:""
            }
            
        },
        
        methods:{
        
            finish:function(){
              sessionStorage.setItem("city",this.city);
               this.$router.push({
                  name:'index',
                  params: { 
                      pro:this.province,
                      city:this.city
                    }
                })
            }
        },
    
        mounted:function(){
           
            this.$nextTick(function(){
                
                var _this = this;
               
                var AREA = null,CITY = null;
                
                $("input[data-label='province']").val("");
                
                $("input[data-label='city']").val("");
                            
                
               $.mobileCityPicker({
                        id: 'citylist',//id
                        inputClass: 'cityPickerInput',//inputclass
                        inputClick: true,//input
                        option:{//option.mobiscroll.treelist
                            defaultValue: [0,0],//
                            label:['province', 'city'],//labelinputdata-label
                            inputClass: 'hidden',//inputclass
                            theme: 'mobiscroll',//
                        mode: 'mixed',//mixed
                        display: 'bottom', //
                        lang: 'zh',//
                        },
                   
                        getTabValue:function(){
                            
                           
                           
                        },
                        callback: function(val,cityes){//valid,citys
                             $("input[data-label='province']").val(arr[0]);
                             $("input[data-label='city']").val(arr[1]);
                   
                   
                             _this.province = arr[0];
                   
                             _this.city = arr[1];
               
                        }
               })
                            
               
            
            })
          
            
        }
    }
</script>
<style coped>
    @import './picker/mobiscroll.frame.css';
    @import './picker/mobiscroll.scroller.css';
    @import './picker/mobiscroll.android-holo-light.css';
    .cityPickerInput{ background: transparent;}
    .mbsc-mobiscroll .dwbc{display: none;}
</style>


